	<div class="layout">
    <Customer_service></Customer_service>
		<Header class="header">中商产品
      <span ><span style="width: 230px;text-align: center;margin: 0 auto;display: inline-block;" @click="changecity">
        <span v-if="site !==undefined">{{site}}</span>
        <span v-if="site ===undefined">切换城市</span>
        +</span></span></Header>
		<Content class="content top_p bottom_p">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="商标" name="name1" span="8">
						<Row type="flex" :gutter="24">
							<Col span="8" v-for="(item, index) in sb_fl" :key="index">
							<p  @click="product_list(item.categoryFir)"><img :src=item.src />{{item.name}}</p>
							</Col>
							<Col span="8">
							<p @click="sb_all"><img src="../../assets/sb_all.png"/>查看全部</p>
							</Col>
						</Row>
					</TabPane>
					<TabPane label="国际商标" name="name2">
						<Row type="flex" :gutter="24">
							<div></div>
							<Col span="8" v-for="(item, index) in gjsb" :key="index">
							<p  @click="gj_product_list(item.areaCode)"><img :src=item.src />{{item.name}}</p>
							</Col>
						</Row></TabPane>
					<TabPane label="名标" name="name3">
						<Row type="flex" :gutter="24">
							<div></div>
							<Col span="8" v-for="(item, index) in mb" :key="index">
							<p  @click="mb_product_list(item.categoryFir)"><img :src=item.src />{{item.name}}</p>
							</Col>
							<Col span="8">
							<p @click="sb_all_mb()"><img src="../../assets/sb_all.png"/>查看全部</p>
							</Col>
						</Row></TabPane>
					<TabPane label="专利" name="name4">
						<Row type="flex" :gutter="16">
							<div></div>
							<Col span="8" v-for="(item, index) in zl_fl" :key="index">
							<p  @click="zl_meau(item.categoryFir,item.value)"><img :src=item.src />{{item.name}}</p>
							</Col>
						</Row>
					</TabPane>
				</Tabs>
			</div>
		</Content>
		<Footer class="footer">
			<Row type="flex">
				<Col span="6">
				<router-link to="/"><img src="../../assets/icon_1.png" /><br />首页</router-link>
				</Col>
				<Col span="6">
				<router-link to="product" class="active"><img src="../../assets/icon_2_hover.png" /><br />商标分类</router-link>
				</Col>
				<Col span="6">
				<router-link to="service"><img src="../../assets/icon_3.png" /><br />服务项目</router-link>
				</Col>
				<Col span="6">
				<a @click="myvip"><img src="../../assets/icon_4.png" /><br />我的</a>
				</Col>
			</Row>
		</Footer>
	</div>
</template>

<script>
  import Customer_service from './../public/Customer_service.vue';
	export default {
		name: 'product_list',
		data() {
			return {
				site:this.$route.query.site,
				sb_fl: [{
