					},
					{
						categoryFir: '02类-涂料油漆',
						name: '02类-涂料油漆',
						src: require('../../assets/sb_2.png'),
					},
					{
						categoryFir: '03类-日化用品',
						name: '03类-日化用品',
						src: require('../../assets/sb_3.png'),
