				],
				zl_fl: [
				{
						categoryFir: '生活需要',
						value:'生活需要',
						name: '生活需要',
						src: require('../../assets/shxy.png'),
					},
					{
						categoryFir: '作业运输',
						value:'作业运输',
						name: '作业运输',
						src: require('../../assets/zyys.png'),
					},
					{
						categoryFir: '化学冶金',
						value:'化学冶金',
						name: '化学冶金',
						src: require('../../assets/hxyj.png'),
					},
					{
						categoryFir: '纺织造纸',
						value:'纺织造纸',
						name: '纺织造纸.',
						src: require('../../assets/fzzz.png'),
					},
					{
						categoryFir: '固定建筑',
						value:'固定建筑',
						name: '固定建筑',
						src: require('../../assets/gdjz.png'),
					},
					{
						categoryFir: '机械工程',
						value:'机械工程',
						name: '机械工程',
						src: require('../../assets/jxgc.png'),
					},
					{
						categoryFir: '物理',
						value:'物理',
						name: '物理',
						src: require('../../assets/wul.png'),
					},
					{
						categoryFir: '电学',
						value:'电学',
						name: '电学',
						src: require('../../assets/dianx.png'),
					},
				]
			}
		},
		components: {
      Customer_service
		},
		methods: {

      open() {
        const h = this.$createElement;
        this.$msgbox({
          title: '消息',
          message: h('p', null, [
            h('span', null, '内容可以是 '),
            h('i', { style: 'color: teal' }, 'VNode')
          ]),
          showCancelButton: true,
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '执行中...';
              setTimeout(() => {
                done();
                setTimeout(() => {
                  instance.confirmButtonLoading = false;
                }, 300);
              }, 3000);
            } else {
              done();
            }
          }
        }).then(action => {
          this.$message({
            type: 'info',
            message: 'action: ' + action
          });
        });
      },
			product_list(categoryFir) {
				if (categoryFir !='all') {
				this.$router.push({
					path: '/sb_list',
                    query:{
                    categoryFir:categoryFir, // orderNum : this.searchData.orderNo
                    applyType:'1',
                    inlandFlag:'Y',
                    areaCode:this.$route.query.site,
                    }
				})
				} else{
					this.$router.push({
					path: '/sb_all',
				})
				}
			},

			mb_product_list(categoryFir) {
				if (categoryFir !='all') {
				this.$router.push({
					path: '/sb_list',
                    query:{
                    categoryFir:categoryFir,
                    vipFlag:'Y',
                    applyType:'1',
                    areaCode:this.$route.query.site,
                    }
				})
				} else{
					this.$router.push({
					path: '/sb_all',
                    query:{
                    vipFlag:'Y',
                    areaCode:this.$route.query.site,
                    }
				})
				}
			},

			gj_product_list(categoryFir) {
				this.$router.push({
					path: '/sb_list',
                    query:{
                    categoryFir:categoryFir, // orderNum : this.searchData.orderNo
                    applyType:'1',
                    inlandFlag:'N',
                    }
				})
			},
			zl_meau(categoryFir,value) {
				this.$router.push({
					path: '/zl_meau',
                    query:{
                    	categoryFir:categoryFir,
                    applyType:'2',
                    value:value,
                    areaCode:this.$route.query.site,
                    }
				})
			},

			sb_all() {
				this.$router.push({
					path: '/sb_all'
				})
			},
			sb_all_mb() {
				this.$router.push({
					path: '/sb_all',
					query:{
                    inlandFlag:'Y',
                    areaCode:this.$route.query.site,
                    }
				})
			},
			myvip() {
				let loginName = localStorage.loginName
				if(loginName != null) {
					this.$router.push({
						path: '/my'
					})
				} else {
					this.$router.push({
						path: '/login'
					})
				}
			},
      changecity() {
        this.$router.push({
          path: '/changecity'
        })
      },
		},
		props: {}
