</script>

<style lang="less" scoped>
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
		p {
			font-size: 14px;
			color: #000000;
			text-align: center;
			margin: 16px 0 26px 0;
			img {
				height: 76px;
				width: 76px;
				display: block;
				margin: 0 auto 5px auto;
			}
		}
	}
</style>



