<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{title}}</Header>
		<Content class="content top_p bottom_p">
			<div class="hm_product_num">
						<ul v-if="sbqg_list.length > 0">
							<li class="item" @click='new_details(item.id)' v-for="(item, name, index) in sbqg_list">
								<p class="title">{{item.name}}</p>
						<span><p class="small_bh">浏览：{{item.total}}人</p>
									<p class="price">发布日期：{{item.createTime}}</p>
									</span>
							</li>
              <Nomore></Nomore>
            </ul>
        <Nochild v-else-if="sbqg_list.length <= 0"></Nochild>
      </div>
		</Content>
	</div>
</template>

<script>
	import { servicegetList } from '../../api/api';
  import Nochild from './../public/Nochild.vue';
  import Nomore from './../public/Nomore.vue';
	export default {
		name: 'com_new',
		data() {
			return {
				value1: 0,
				title:'公司动态',
				setting: {
					autoplay: false,
					autoplaySpeed: 2000,
					dots: 'inside',
					radiusDot: false,
					height: 267,
					trigger: 'click',
					arrow: 'hover'
				},
				sbqg_list: []
			}
		},
		components: {
      Nochild,Nomore
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//获取商标知识信息
			getsbknow() {
				let para = {
					type:'5',
				};
				this.listLoading = true;
				//NProgress.start();
				servicegetList(para).then((res) => {
					this.sbqg_list = res.data.result.result;
					this.total = res.data.result.total;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			new_details(id) {
				this.$router.push({
					path: '/new_details',
					query: {
						id: id,
						type: '5',
					}
				})
			},
		},
		props: {},
		mounted() {
			//this.page = Number(localStorage.pageIndex);
			this.getsbknow();
		}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
		li {
			flex: 2;
			font-size: 18px;
			color: #000000;
			width: 100%;
			line-height: 22px;
			text-align: left;
			padding:  5px 0  15px 0 ;
			margin-bottom: 20px;
			border-bottom: 1px solid #E6E6E6;
			font-weight: bold;
			span {
				width: 100%;
				display: block;
				width: 100%;
				display: flex;
				justify-content: space-between;
				line-height: 36px;
				font-size: 15px;
				color: #666666;
			font-weight: normal;
				.small_bh {
					text-align: left;
					flex: 1;
					align-items: left;
				}
				.price {
					text-align: right;
					flex: 1;
					align-items: right;
				}
			}
		}
	}
</style>



