<template>
	<div class="layout">
		<!--<Header class="header2">-->
			<!--<p class="goback" @click='prev'>-->
				<!--<Icon type="ios-arrow-back" slot="prefix" />-->
			<!--</p>{{service.name}}</Header>-->
    <div class="bg_box">
      <p class="goback" @click='prev'>
      <Icon type="ios-arrow-back" slot="prefix" />
    </p>
      <img :src=service.bannerUrl />
    </div>
    <div style="height: 110px;display: block;" v-show="id=='34'"></div>
      <Content :model="service" style="margin-top:88px">
    <div class="line"></div>
          <div class="hm_product_num">
              <Row type="flex" :gutter="24" class="de_cont"  v-if="id != '34'">
                  <Col span="12">
                  <p>价格: <span class="cl_num">{{service.price}}</span></p>
                  </Col>
                  <Col span="12">
                  <p>商品编号:{{service.id}}</p>
                  </Col>
              </Row>
              <Row type="flex" :gutter="24" class="de_cont ">
                  <Col span="24">
          <p class="imgbox"><img src="../../assets/nr1.jpg" /></p>
                  <p class="cont" v-html='service.content' ></p>
          <p class="imgbox"><img src="../../assets/nr2.jpg" /></p>
          <p class="imgbox"><img src="../../assets/nr3.jpg" /></p>
          <p class="imgbox"><img src="../../assets/nr4.jpg" /></p>
          <p class="imgbox"><img src="../../assets/nr5.jpg" /></p>
                  </Col>
              </Row>
            <div style="height: 71px;display: block;" v-show="id!= '34'"></div>
          </div>
      </Content>
      <buyservice :name="name" :price="newprice" v-if="id != '34'"></buyservice>
  </div>
</template>

<script>
  import { servicegetList, servicecreate, serviceupdate, servicedelete, servicegetById } from '../../api/api';
  import buyservice from './../public/buyservice.vue';
  export default {
      name: 'service_details',
      data() {
          return {
              name:this.name,
              price:this.price,
      newprice:this.newprice,
      id:this.$route.query.id,
              total:'',
              content: '',
              bannerUrl:'',
              service:[],
          }
      },
      components: {
          buyservice
      },
      methods: {
          prev() {
              this.$router.go(-1)
          },
          //获取产品类别列表
          getservice() {
              let para = {
                  id:this.$route.query.id
              };
              this.listLoading = true;
              servicegetById(para).then((res) => {
                  this.service = res.data.result;
                  this.price = this.service.price;
                  this.newprice = Number(this.price) - Number(localStorage.brand_fee);
                  this.name = this.service.name;
                  this.$route.query.price=this.price;
                  this.listLoading = false;
              });
          },
      },
      props: {},
      mounted() {
          this.getservice();
      }
  }
</script>

<style lang="less" scoped>
  .bg_box {
  width: 130%;
  margin-left: -15%;
  display: block;
  font-size: 23px;
  position: fixed;
  color: #FFFFFF;
  text-align: center;
  font-weight: bolder;
  z-index: 99;
    .goback{
      position: absolute;
      top: 15px;
      left: 15%;
      font-size: 22px;
      z-index: 999;
    }
  }

  .top {
      margin-top: 152px;
  }

  .hm_product_num {
      width: 100%;
      display: block;
      height: auto !important;
      min-height: 20px;
      background-color: #FFFFFF;
      overflow: hidden;
      .de_cont {
          padding:5px;
      }
      .cl_num{
          color: #2B2EF5;
          font-size: 26px;
      }
      span {
          display: inline-block;
          text-align: left;
          font-weight: bold;
      }
  .imgbox{
    display: flex;
    width: 100%;
    justify-content: center;
    padding: 0;
    img{
      width: 130%;
    }
  }
      p {
          font-size: 15px;
          color: #000000;
          line-height: 22px;
          text-align: left;
          padding: 7px;
    img{
      width: 200%;
    }
      }
      .cont {
          font-size: 14px;
          color: #000000;
          width: 100%;
          line-height: 25px;
      }
      .line {
          width: 100%;
          background-color: #F6F6F6;
          height: 1px;
          padding: 0;
      }
  }
</style>



