<template>
		<Footer class="footer">
			<Row type="flex">
				<Col span="6">
				<router-link to="/" class="active"><img src="../../assets/icon_1.png" /><br />首页</router-link>
				</Col>
				<Col span="6">
				<router-link to="product"><img src="../../assets/icon_2.png" /><br />商标分类</router-link>
				</Col>
				<Col span="6">
				<router-link to="service"><img src="../../assets/icon_3.png" /><br />服务项目</router-link>
				</Col>
				<Col span="6">
				<a @click='myvip'><img src="../../assets/icon_4.png" /><br />我的</a>
				</Col>
			</Row>
		</Footer>
</template>

<script>
	export default {
		name: 'footmeau',
		data() {
			return {
			}
		},
		computed:{
		},
		methods:{
			myvip() {
				let loginName=localStorage.loginName
				if(loginName != null){
				this.$router.push({
					path: '/my'
				})
				}else{
				this.$router.push({
					path: '/login'
				})
				}
			},
		}
	}
</script>

<style>
</style>



