<template>
	<div class="layout">
    <Customer_service></Customer_service>
		<div class="ser_banner">
			<p class="ser_header">中商服务</p>
			<p class="big_title">服务专业 省心省力</p>
			<p class="banner_p">多个合作伙伴 最快响应速度 最便捷服务通道</p>
		</div>
		<Content>
			<div class="hm_product_num bottom_p">
				<Row type="flex" class="four_sever" :gutter="16">
					<Col span="6" v-for="(item, index) in service_list" :key="index">
					<p @click='ser_deta(item.id,item.name)' :key="index"><span  :class="item.class" >{{item.tit}}</span>{{item.name}}</p>
					</Col>
				</Row>
				<Row class="sev_list">
					<Col span="12" v-for="(item, index) in service_list2" :key="index">
					<span  @click='ser_deta(item.id)'>{{item.name}}</span>
					</Col>
				</Row>
				<Row type="flex" class="four_sever" :gutter="16">
					<Col span="6" v-for="(item, index) in service_list3" :key="index">
            <p @click='ser_deta(item.id)' :key="index"><span  :class="item.class" >{{item.tit}}</span>{{item.name}}</p>
					</Col>
				</Row>
			</div>
		</Content>
		<Footer class="footer">
			<Row type="flex">
				<Col span="6">
				<router-link to="/"><img src="../../assets/icon_1.png" /><br />首页</router-link>
				</Col>
				<Col span="6">
				<router-link to="product"><img src="../../assets/icon_2.png" /><br />商标分类</router-link>
				</Col>
				<Col span="6">
				<router-link to="service" class="active"><img src="../../assets/icon_3_hover.png" /><br />服务项目</router-link>
				</Col>
				<Col span="6">
				<a @click="myvip"><img src="../../assets/icon_4.png" /><br />我的</a>
				</Col>
			</Row>
		</Footer>
	</div>
</template>

<script>
  import Customer_service from './../public/Customer_service.vue';
	export default {
		name: 'home',
		data() {
			return {
				service_list:[
          {
            tit:'注',
            id: '35',
            name: '商标注册',
            class:'sty1'
          },
          {
            tit:'转',
            id: '36',
            name: '商标转让',
            class:'sty2'
          },
          {
            tit:'驳',
            id: '37',
            name: '驳回复审',
            class:'sty3'
          },
          {
            tit:'异',
            id: '38',
            name: '商标异议',
            class:'sty4'
          },
          {
            tit:'效',
            id: '39',
            name: '无效宣告',
            class:'sty2'
          },
          {
            tit:'撤销',
            id: '40',
            name: '三年不使用',
            class:'sty3'
          },
          {
            tit:'变',
            id: '41',
            name: '商标变更',
            class:'sty4'
          },
          {
            tit:'答',
            id: '42',
            name: '案件答辩',
            class:'sty1'
          },
          {
            tit:'续',
            id: '43',
            name: '商标续展',
            class:'sty2'
          },
          {
            tit:'备',
            id: '44',
            name: '许可备案',
            class:'sty3'
          }
        ],

				service_list2:[
          {
            id: '45',
            name: '各种疑难处理',
            class:'sty1'
          },
          {
            id: '46',
            name: '商标维权',
            class:'sty2'
          },
          {
            id: '47',
            name: '防范侵权培训',
            class:'sty4'
          },
          {
            id: '48',
            name: '商标托管',
            class:'sty1'
          },
          {
            id: '49',
            name: '案件诉讼',
            class:'sty1'
          },
          {
            id: '50',
            name: '无区域名称核准',
            class:'sty2'
          }
				],
				service_list3:[
          {
            tit:'登',
            id: '51',
            name: '版权登记',
            class:'sty3'
          },
          {
            tit:'专',
            id: '52',
            name: '专利',
            class:'sty4'
          },
          {
            tit:'码',
            id: '53',
            name: '条码申请',
            class:'sty1'
          },
          {
            tit:'销',
            id: '54',
            name: '商标注销',
            class:'sty2'
          }
				]
			}
		},
		components: {
      Customer_service
		},
		methods: {
			ser_deta(id,name) {
				this.$router.push({
					path: '/service_details',
					query:{
						id:id,
						name:name,
						applyType:'0'
					}
				})
			},
			myvip() {
				let loginName=localStorage.loginName
				if(loginName != null){
				this.$router.push({
					path: '/my'
				})
				}else{
				this.$router.push({
					path: '/login'
				})
				}
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
  img{
    width: auto;
  }
  .layout {
    background-color: #ffffff;
  }
	.ser_banner {
		background-image: url(../../assets/ser_banner.png);
    background-size: 100% 100%;
		height: 150px;
		width: 100%;
		display: block;
		.ser_header {
			font-size: 18px;
			width: 100%;
			color: #fff;
			font-weight: bold;
			padding: 0 20px;
			height: 60px;
			line-height: 60px;
			display: block;
			text-align: left;
		}
		.big_title {
			font-family: PingFang-SC-Bold;
			font-size: 26px;
			color: #FFFFFF;
		}
		.banner_p {
			font-family: PingFang-SC-Regular;
			font-size: 13px;
			color: #FFFFFF;
		}
	}

	.hm_product_num {
		width: 100%;
		padding:0 0 20px 0;
		display: block;
		height: auto !important;
		min-height: 510px;
		background-color: #FFFFFF;
		overflow: hidden;

    .sty1{
      background-color: #52b998;
      color: #ffffff;
      font-size: 19px;
      font-weight: bolder;
      display: block;
      width: 50px;
      height: 50px;
      border-radius: 50px;
      margin: 0 auto 5px auto;
      line-height: 50px;
    }
    .sty2{
      background-color: #1969f6;
      color: #ffffff;
      font-size: 19px;
      font-weight: bolder;
      display: block;
      width: 50px;
      height: 50px;
      border-radius: 50px;
      margin: 0 auto 5px auto;
      line-height: 50px;
    }
    .sty3{
      background-color: #5f42e2;
      color: #ffffff;
      font-size: 19px;
      font-weight: bolder;
      display: block;
      width: 50px;
      height: 50px;
      border-radius: 50px;
      margin: 0 auto 5px auto;
      line-height: 50px;
    }
    .sty4{
      background-color: #ee5b36;
      color: #ffffff;
      font-size: 19px;
      font-weight: bolder;
      display: block;
      width: 50px;
      height: 50px;
      border-radius: 50px;
      margin: 0 auto 5px auto;
      line-height: 50px;
    }
    .sty31{
      background-color: #062640;
      color: #ffffff;
      font-size: 19px;
      font-weight: bolder;
      display: block;
      width: 50px;
      height: 50px;
      border-radius: 50px;
      margin: 0 auto 5px auto;
      line-height: 50px;
    }
		.four_sever {
			margin: 0;
			padding: 0 20px;
			display: flex;
			font-size: 14px;
			color: #000000;
			text-align: center;
			p {
				justify-content: center;
				margin-top: 15px;
				text-align: center;
			}
		}
		.sev_list {
			margin: 15px 0 0px 5px;
			span {
				display: block;
				font-size: 16px;
				color: #FFFFFF;
				line-height: 50px;
				height: 50px;
				background-image: url(../../assets/sev_bg2.png);
				background-repeat: no-repeat;
				background-size:100% 100%;
				margin: 0 5px 5px 0;
				text-align: left;
				text-indent: 35px;
				padding: 0;
			}
			.bg2 {
				display: block;
				font-size: 16px;
				color: #FFFFFF;
				line-height: 50px;
				height: 50px;
				background-image: url(../../assets/sev_bg1.png);
				background-repeat: no-repeat;
				background-size:100% 100%;
				margin: 0 5px 5px 0;
				text-align: left;
				text-indent: 35px;
				padding: 0;
			}
		}
	}
</style>



