<template>
	<Footer class="footer">
		<Row type="flex" v-if="applyType!='0'">
			<Col span="12">
        <p @click="djcz" v-if="customerId ==undefined && customerId ==null"  style="cursor: pointer"><img :src=sczt style="margin-right: 10px;" />{{mes}}</p>
        <p @click="djcz" v-if="this.flag ==false && customerId !=undefined && customerId !=null"  style="cursor: pointer"><img :src=sczt style="margin-right: 10px;" />{{mes}}</p>
        <p @click="qxdjcz"  v-else-if="this.flag ==true && customerId !=undefined && customerId !=null " style="cursor: pointer"><img :src=nosczt style="margin-right: 10px;" />{{nomes}}</p>
      </Col>
			<Col span="12">
			<p class="buy" @click="ok"><img src="../../assets/goumai.png" />购买</p>
			</Col>
		</Row>
    <Row type="flex" v-if="applyType=='0'">
      <Col span="24">
        <p class="buy" @click="ok"><img src="../../assets/goumai.png" />购买</p>
      </Col>
    </Row>
	</Footer>
</template>

<script>
	import {
		Collectcreate,
		CollectgetById,
		Collectdelete
	} from '../../api/api';
	export default {
		name: 'buyservice',
		data() {
			return {
        sczt: require('../../assets/shoucang.png'),
        nosczt: require('../../assets/ysc@2x.png'),
        mes: '收藏',
        nomes: '取消收藏',
        applyType:this.$route.query.applyType,
        customerId:localStorage.customerId
			}
		},
    props:{
      price: {
        type: Number,
        default: 0
      },
      flag: {
        type:Boolean,
        default: false
      },
      name: {
        type: String,
        default: ''
      }
    },
		methods: {
			onload() {
        let applyType = this.applyType
				if(localStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				} else {
				}
			},
			ok() {
        this.onload();
				this.$Message.success('开始订单支付');
				this.$router.push({
					path: '/zhifuding',
					query: {
						id: this.$route.query.id,
						applyType: this.$route.query.applyType,
						name: this.$route.query.name,
            price: this.price,
					}
				})
			},
			cancel() {
				this.$Message.warning('取消操作');
			},
      djcz(){
        this.onload();
        let applyType = this.$route.query.applyType;
        let para = {
          actionType: '2',
          contentType: applyType,
          targetId: this.$route.query.id,
          id: this.$route.query.id,
          customerId: localStorage.customerId
        };
        Collectcreate(para).then((res) => {
          this.listLoading = false;
          if(res.data.code == '0') {
            this.$Message.success({
              content: '收藏成功',
              duration: 3
            });
            this.newflag = true
            this.sczt= this.nosczt
            this.mes=this.nomes
          } else {
            this.$Message.warning({
              content: '收藏失败',
              duration: 3
            });
          }
          //NProgress.done();
        }).catch((err) => {
          console.log(err);
        })
      },
      qxdjcz(){
        this.onload();
        let applyType = this.$route.query.applyType;
        //NProgress.start();
        let para = {
          actionType: '2',
          contentType: applyType,
          targetId: this.$route.query.id,
          id: this.$route.query.id,
          customerId: localStorage.customerId
        };
        Collectdelete(para).then((res) => {
          this.products = res.data.result;
          this.listLoading = false;
          if(res.data.code == '0') {
            this.$Message.success({
              content: '取消收藏成功',
              duration: 3
            });
            this.nosczt= this.sczt
            this.nomes=this.mes
          } else {
            this.$Message.warning({
              content: '取消收藏失败',
              duration: 3
            });
          }
          //NProgress.done();
        }).catch((err) => {
          console.log(err);
        })
      }
		},
		mounted() {
		},
		watch: {
			'sczt': function(newVal) {
				this.sczt = newVal
			},
			'mes': function(newVal) {
				this.mes = newVal
			},
		}
	}
</script>

<style lang="less" scoped>
	.tkwz {
		font-size: 18px;
		color: #000000;
	}

	.footer {
		background-color: #FFFFFF;
		z-index: 999;
		bottom: -1px;
		width: 100%;
		position: fixed;
		padding: 0;
		box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.3);
		p {
			line-height: 60px;
			text-align: center;
			color: #000;
			font-size: 18px;
		}
		img {
			height: 23px;
			display: inline-block;
			margin: auto;
			vertical-align: middle;
		}
		.line {
			margin-left: -1px;
			margin-top: 15px;
			height: 30px;
			width: 1px;
			display: inline-block;
			vertical-align: middle;
			background-color: #F6F6F6;
		}
		.buy {
			background-color: #FF5D00;
			display: block;
			height: 60px;
			color: #FFFFFF;
			font-size: 18px;
			img {
				display: inline-block;
				margin-right: 5px;
				vertical-align: text-top;
			}
		}
	}
</style>



